
@mixin placeholderMixin($color){
    &:placehoder {
        color: $color;
    }

    &::-webkit-input-placeholder {
        color: $color;
    }

    &::-moz-placeholder {
        color: $color;
    }

    &:-ms-input-placeholder {
        color: $color;
    }

    &:-moz-placeholder {
        color: $color;
    }
}